body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-header{
    height: 6rem;
    background-color: cornflowerblue;
    color: white;
    border-bottom: 5px solid black;
    } 
    
    .app-header h1{
        font-size: 3rem;
        line-height: 6rem;
        text-align: center;
    }

    h1{
        text-align: center;
    }

    .backBtn{
        float: left;
        text-decoration: underline;
        font-size: 2rem;
        line-height: 4rem;
        margin-left: 1rem;
    }

    .loading{
        text-align: center;
        margin-top: 0.5rem;
        -webkit-animation: throbbing 1s infinite ease-in-out;
                animation: throbbing 1s infinite ease-in-out;
    }
    
    @-webkit-keyframes throbbing{
        0%{
            font-size: 2rem;
        }
        50%{
            font-size: 3rem;
        }
        100%{
            font-size: 2rem;
        }
    }
    
    @keyframes throbbing{
        0%{
            font-size: 2rem;
        }
        50%{
            font-size: 3rem;
        }
        100%{
            font-size: 2rem;
        }
    }
.split {
    /* height: 100%; */
    width: 100%;
    position: relative;
    z-index: 1;
    /* top: 7.5rem; */
    /* overflow-x: hidden; */
    /* padding-top: 20px; */
}

.list-container {
    box-sizing: border-box;
    left: 0;
    list-style: none;
    padding: 0 10px 0 10px;
}
.item{
    padding: 5px;
    border: 2px solid #bbb;
    border-radius: 8px;
    min-height: 6rem;
    position: relative;
    margin-bottom: 10px;
}

p, h1{
    padding: 0;
    margin: 0;
}

p{
    font-size:1.5rem;
}
.input-field{
    border: 1px solid #aaa;
    margin-bottom: 5px;
    font-size: 1.5rem;
    
}

.item-container{
    display: flex;
    flex-direction: column;
    width: 75%;
    min-height: 6rem;
    position: absolute;
    top: 5px;
    left: 5px;
    justify-content: space-around;
    align-items: center;
}

.hide{
    display: none;
}

.button-container{
    display: flex;
    flex-direction: column;
    width: 20%;
    position: absolute;
    top: 5px;
    right: 5px;
    min-height: 6rem;
    justify-content: space-around;
    align-items: flex-start;
}

.button{
    background-color: white;
    -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s;
    cursor: pointer;
    border-radius: 5px;
    font-size: 1.2rem;
}

.button-edit{
    color: black;
    border: 2px solid #e7e7e7;
}

.button-edit:hover{
    background-color: #e7e7e7
}

.button-delete{
    color: black;
    border: 2px solid #f44336;
}

.button-delete:hover{
    background-color: #f44336;
    color: white;
}

.button-save{

    color: black; 
    border: 2px solid #4CAF50;
}

.button-save:hover{
    background-color: #4CAF50;
    color: white;
}
.loading{
    text-align: center;
    margin-top: 0.5rem;
    -webkit-animation: throbbing 1s infinite ease-in-out;
            animation: throbbing 1s infinite ease-in-out;
}

@-webkit-keyframes throbbing{
    0%{
        font-size: 2rem;
    }
    50%{
        font-size: 3rem;
    }
    100%{
        font-size: 2rem;
    }
}

@keyframes throbbing{
    0%{
        font-size: 2rem;
    }
    50%{
        font-size: 3rem;
    }
    100%{
        font-size: 2rem;
    }
}

.itemTodo{
    padding: 5px;
    border: 2px solid #bbb;
    border-radius: 8px;
    min-height: 8rem;
    position: relative;
    margin-bottom: 10px;
    overflow: scroll;
}

p, h1{
    padding: 0;
    margin: 0;
}

p{
    font-size:1.5rem;
    padding: 10px;
}

.todo-container{
    display: flex;
    flex-direction: column;
    width: 80%;
    min-height: 6rem;
    position: absolute;
    top: 5px;
    left: 5px;
    justify-content: space-around;
    align-items: center;
}

.id-container{
    display: flex;
    flex-direction: column;
    width: 10%;
    position: absolute;
    top: 5px;
    right: 5px;
    min-height: 6rem;
    justify-content: space-around;
    align-items: flex-start;
}

.green{
    color: royalblue
}

.red{
    color: red;
}
